<template>
    <div class="playe-container w">
        <div class="video-body">
            <!-- 播放器 -->
            <VideoPlaye @movieName="getVideoName"/>
            <!-- 右侧会员 -->
            <OpenVip/>
        </div>
    </div>
</template>

<script>
import VideoPlaye from "@/components/Playe/VideoPlaye.vue"
import OpenVip from "@/components/Playe/OpenVip.vue"

export default {
    components: {
        VideoPlaye,
        OpenVip
    },
    methods: {
        // 用于 接收子组件 传过来的视频标题
        getVideoName(val) {
            // 设置 浏览器 title 为当前播放的视频标题
            window.document.title = val; // 标题
        }
    },
    created() {
        this.getVideoName()
    }
};
</script>

<style lang="less" scoped>
.playe-container {
    padding-top: 135px;
    .video-body {
        position: relative;
        height: 610px;
        margin-top: 10px;
        margin-bottom: 15px;
    }
}
</style>