<template>
    <div class="userinfo-container">
        <!-- 用户信息 -->
        <section class="ui-page">
            <div class="pep">
                <div class="consumer">
                    <div class="userPic">
                        <img v-if="user" :src="user.avatar ? user.avatar : userIcon" />
                        <img v-else src="../../assets/user.png" />
                    </div>
                    <p class="uname">ID：
                        {{ user ? user.nickname || user.username : userName }}
                    </p>
                </div>
            </div>
        </section>
        <!-- 用户权限信息 -->
        <section class="authority">
            <ul class="vipBox">
                <li class="grade">
                    <p class="dj">等级</p>
                    <p v-if="user" class="isvip">{{ user.is_vip === 1 ? "会员用户" : "普通用户" }}</p>
                    <p v-else class="isvip">普通用户</p>
                </li>
                <li class="prerogativ">
                    <p class="dj">特权</p>
                    <p v-if="user" class="isvip">{{ user.is_vip === 1 ? "无限观看" : "免费试看"}}</p>
                    <p v-else class="isvip">免费试看</p>
                </li>
            </ul>
            <div class="buy-vip" v-if="user">
                <img v-if="user.is_vip === 1" src="../../assets/is_vip.png" alt="" />
                <img v-else src="../../assets/no_vip.png" alt="" />
            </div>
            <div class="buy-vip" v-else>
                <img src="../../assets/no_vip.png" alt="" />
            </div>
            <div class="openVip">
                <span class="ico_fly"></span>
                <a href="#" class="vip_text">开通VIP会员</a>
            </div>
        </section>
    </div>
</template>

<script>
// mapState作用：获取 store/index.js 中 state 中的数据
import { mapState } from 'vuex'

export default {
    name: "UserInfo",
    data() {
        return {
            userIcon: "https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png",
            userName: "游客",
        }
    },
    // mounted() {
    //     this.$store.dispatch('getUser')
    //     console.log(this.user) 
    // },
    computed: {
        // user 为 store/index.js 中 state 中的属性名
        // mapState 函数：将 state 中的 user 数据，映射为当前组件的计算属性
        // 映射后可以直接渲染（相当于$store.state.user）
        ...mapState(['user'])
    },
};
</script>

<style lang="less" scoped>
// 用户信息
.consumer {
    padding: 9px;
    // 头像
    .userPic {
        margin-top: 10px;
        text-align: center;
        img {
            width: 78px;
            height: 78px;
            border-radius: 50%;
            border: 2px solid #ccc;
            // 基线对齐
            vertical-align: middle;
            // 自适应填满盒子
            object-fit: cover;
        }
    }
    // 用户名
    .uname {
        display: block;
        height: 24px;
        margin-top: 10px;
        color: #fb7299;
        text-shadow: 0px 0px 6px rgba(0, 0, 0, 0.6);
        font-size: 16px;
        text-align: center;
        overflow: hidden;
        text-overflow: ellipsis; // 溢出用省略号显示
        white-space: nowrap; // 溢出不换行
    }
}
// 用户权限信息
.authority {
    margin-top: 38px;
    height: 222px;
    // background-color: orange;
    .vipBox {
        display: flex;
        height: 41px;
        li {
            flex: 1;
            display: inline-block;
            text-align: center;
            overflow: hidden;
            p {
                padding: 0 12px;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
            }
        }
        .grade {
            border-right: 1px dashed #34425a;
        }
        .prerogativ {
            border-left: 1px dashed #34425a;
        }
        .dj {
            font-size: 14px; color: #878787
        }
        .isvip {
            font-size: 16px; color: #f66
        }
    }
    .buy-vip {
        height: 80px;
        text-align: center;
        margin-top: 20px;
        overflow: hidden;
    }
    .openVip {
        position: relative;
        margin-top: 35px;
        height: 36px;
        text-align: center;
        .vip_text {
            display: block;
            width: 124px;
            height: 100%;
            color: #fff;
            font-size: 14px;
            line-height: 36px;
            border-radius: 18px;
            margin: 0 auto;
            background: #ff9800;
        }
        // 翅膀
        .ico_fly:before {
            content: "";
            position: absolute;
            top: 7px;
            left: 32px;
            background: url(../../assets/payvip.png) no-repeat -60px 0;
            width: 13px;
            height: 24px;
            transform-origin: right bottom;
            -webkit-transform-origin: right bottom;
            animation: super_ngiftl 2s linear both infinite;
            -webkit-animation: super_ngiftl 2s linear both infinite;
        }
        .ico_fly:after {
            content: "";
            position: absolute;
            top: 7px;
            right: 32px;
            background: url(../../assets/payvip.png) no-repeat -100px 0;
            width: 13px;
            height: 24px;
            transform-origin: left bottom;
            -webkit-transform-origin: left bottom;
            animation: super_ngiftr 2s linear both infinite;
            -webkit-animation: super_ngiftr 2s linear both infinite;
        }
        // 翅膀动画
        @keyframes super_ngiftl {
            0%,
            100% {
                transform: rotate(0deg);
            }
            50% {
                transform: rotate(-15deg);
            }
        }
        @-webkit-keyframes super_ngiftl {
            0%,
            100% {
                -webkit-transform: rotate(0deg);
            }
            50% {
                -webkit-transform: rotate(-15deg);
            }
        }
        @keyframes super_ngiftr {
            0%,
            100% {
                transform: rotate(0deg);
            }
            50% {
                transform: rotate(15deg);
            }
        }
        @-webkit-keyframes super_ngiftr {
            0%,
            100% {
                -webkit-transform: rotate(0deg);
            }
            50% {
                -webkit-transform: rotate(15deg);
            }
        }
        @keyframes super_giftl {
            0%,
            100% {
                transform: rotateY(0deg);
            }
            50% {
                transform: rotateY(-50deg);
            }
        }
    }
}
</style>