<template>
    <div class="user-history">
        <!-- 视频列表分类 -->
        <el-row :gutter="15">
            <VideoInfo
                v-for="item in historyList"
                :key="item.id"
                :span="6"
                :id="item.id"
                :title="item.title"
                :pic="item.thumb"
                :longUrl="item.video_url"
                :shortUrl="item.short_video_url"
                :view="item.view_num"
                :like="item.like_num"
                :tags="item.tags"
                :time="item.mtime"
                :category="item.category_name"
                :topic_info="item.topic_info"
            >
            </VideoInfo>
        </el-row>
    </div>
</template>

<script>
export default {
    data() {
        return {
            historyList: [], // 历史观看记录
        }
    },
    methods: {
        // 添加历史记录
        historyAdd() {
            this.$http.user.userHistoryAdd({video_id: 1}).then(({data})=> {
                console.log(data);
            })
        },
        getHistoryVideo() {
            this.$http.user.userHistory().then(({data}) => {
                for(var i = 0; i < data.list.length; i++) {
                    console.log(data.list[i].video);
                    this.historyList = this.historyList.concat(data.list[i].video)
                }
                console.log(this.historyList);
            })
        }
    },
    created() {
        // this.historyAdd()
        // this.getHistoryVideo()
    }
}
</script>

<style lang="less" scoped>
.user-history {
    width: 100%;
    height: 300px;
    background-color: orange;
}
</style>