<template>
    <div class="category-container">
        <!-- 影片分类 -->
        <div class="category_content">
            <div class="category_bj">
                <img :src="bjPic" alt="" />
            </div>
            <span class="total">{{ total }}</span>
            <h1 class="archive-title">{{ classifyTitle }}</h1>
            <!-- Tab 切换栏：分类切换 -->
            <div class="category_class">
                <!-- 一级分类 -->
                <div class="filter-item">
                    <ul class="filter-tag">
                        <div class="g-class-top">
                            <i class="el-icon-menu"></i>
                            <span>分类</span>
                        </div>
                        <li 
                            v-for="item in dts" 
                            :key="item.id"
                            @click="change(item.id, item.icon, item.name)"
                            :class="{ active: item.id == categoryId }">
                            {{ item.name }}
                        </li>
                    </ul>
                </div>
                <!-- 二级分类 -->
                <div class="filter-item">
                    <ul class="filter-tag">
                        <div class="g-class-top">
                            <i class="el-icon-s-grid"></i>
                            <span>细分</span>
                        </div>
                        <li 
                            v-for="item in dts2" 
                            :key="item.id"
                            @click="change2(item.id, item.name)"
                            :class="{ active: item.id == subCategoryID }">
                            {{ item.name }}
                        </li>
                    </ul>
                </div>
                <!-- 排序方式 -->
                <div class="filter-item">
                    <ul class="filter-tag">
                        <div class="g-class-top">
                            <i class="el-icon-s-data"></i>
                            <span>排序</span>
                        </div>
                        <li 
                            v-for="(item, index) in range" 
                            :key="index"
                            @click="change3(index)"
                            :class="{ active: rangeIndex == index }">
                            {{ item }}
                        </li>
                    </ul>
                </div>
            </div>
        </div>
        <!-- 视频列表分类 -->
        <div class="category_list w">
            <el-row :gutter="15">
                <VideoInfo
                    v-for="item in classifyVideo"
                    :key="item.id"
                    :span="6"
                    :id="item.id"
                    :title="item.title"
                    :pic="item.thumb"
                    :longUrl="item.video_url"
                    :shortUrl="item.short_video_url"
                    :view="item.view_num"
                    :like="item.like_num"
                    :tags="item.tags"
                    :time="item.mtime"
                    :category="item.category_name"
                    :topic_info="item.topic_info"
                    :is_like="item.is_like"
                >
                </VideoInfo>
            </el-row>
        </div>
        <!-- 分页功能：使用 ElementUi -->
        <div class="pager-box">
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="pagenum"
                :page-sizes="[16, 20, 32, 60]"
                :page-size="pagesize"
                :total="total"
                layout="total, sizes, prev, pager, next"
                prev-text="上一页"
                next-text="下一页"
                background
                style="text-align: center"
                :hide-on-single-page="true"
            >
            </el-pagination>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            dts: [],
            fristDt:{ id: 0, name: "全部", icon: "http://121.4.184.254/images/1.jpg" },
            // 分类id：用于tab栏选中状态 和 切换请求数据
            categoryId: 0, // 一级分类
            classifyVideo: [],
            classifyTitle: "全部", // 分类标题
            bjPic: "http://121.4.184.254/images/1.jpg",
            // 二级分类
            dts2: [{id: null, name: "暂无子类"}],
            subCategoryID: 0, // 二级分类
            order: "", // 0时间排序、1点赞排序、2浏览排序
            // 排序
            rangeIndex: 0,
            range: ["最新更新", "最多喜欢", "最多播放"],
            // 分页功能
            pagenum: 1, // 页数
            pagesize: 16, // 每页展示多少条数据
            total: 0, // 总条数
            mid: 0,
        };
    },
    methods: {
        // Tab 栏选中状态
        change(id, pic, title) {
            this.categoryId = id;
            this.classifyTitle = title;
            this.bjPic = pic;
            this.pagenum = 1; // 切换Tab栏时,让页码值为1
            this.subCategoryID = ""
        },
        change2(id, title) {
            // this.categoryId = id;
            this.subCategoryID = id;
            // this.classifyTitle = title;
            this.pagenum = 1; // 切换Tab栏时,让页码值为1
        },
        change3(index) {
            this.rangeIndex = index
            // this.pagenum = 1;
            this.getCategoryVideo(this.mid);
        },
        // 发起 axios 请求：获取所有的分类
        getCategory () {
            this.$http.video.category().then(({ data }) => {
                this.dts = data;
                this.dts.unshift(this.fristDt) // 向数组头部追加一个元素
                this.categoryId = this.dts[0].id; // 把 第一个分类的id 赋值给 categoryId
            })
        },
        // 发起 axios 请求：获取 分类列表数据 的方法
        getCategoryVideo (mid) {
            this.$http.video.list({
                page: this.pagenum, // 页数
                limit: this.pagesize, // 每页条数
                category_id: mid, // 分类id
                order: this.rangeIndex // 排序方式
            }).then(({ data }) => {
                this.classifyVideo = data.list;
                this.total = data.total; // 当前分类视频总条数
                if(this.dts2===undefined || this.dts2.length<1) {
                    this.dts2 = [{id: null, name: "暂无子类"}];
                }
            })
        },
        //-------------------分页功能-------------------
        // pageSize 改变时会触发
        handleSizeChange(newSize) {
            // console.log(`每页 ${newSize} 条`);
            this.pagesize = newSize;
            this.getCategoryVideo(this.mid, this.topicID);
        },
        // currentPage 改变时会触发
        handleCurrentChange(newPage) {
            // console.log(`当前页: ${newPage}`);
            this.pagenum = newPage;
            this.getCategoryVideo(this.mid, this.topicID);
        },
    },
    // 侦听器：监听 categoryId 值的变化,从而请求到不同的数据
    watch: {
        // 对象格式的侦听器：监听 categoryId 值的变化
        categoryId: {
            // handler 是固定写法,表示当 categoryId 的值发生变化,自动调用 handler 处理函数
            handler(newID) {
                this.mid = newID
                this.categoryId = newID;
                this.subCategoryID = newID
            },
            // immediate: true, // 进入页面时自动触发一次
        },
        subCategoryID: {
            handler(newID) {
                this.mid = newID
                this.subCategoryID = newID;
                this.getCategoryVideo(newID);
                this.dts2 = this.dts[this.categoryId].children;
            },
        },
    },
    mounted() {
        this.getCategory();
        this.getCategoryVideo();
    },
};
</script>

<style lang="less" scoped>
// 待添加类
.active {
    color: #f3f3f3 !important;
    background-color: #fb7299 !important;
    border: 1px solid transparent !important;
    // font-size: 30px !important;
    // filter: brightness(100%) !important;
    // box-shadow: 0px 12px 8px -12px #000;
}
.category-container {
    padding-top: 135px;
    .category_content {
        position: relative;
        height: 350px;
        // background-color: green;
        .category_bj {
            position: relative;
            width: 100%;
            height: 100%;
            overflow: hidden;
            // background-color: orange;
            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                // filter: blur(10px); // 模糊度
                // -webkit-filter: blur(10px);
                // 使用动画
                animation: move 25s linear 2 alternate;
                animation-iteration-count: infinite; // 无限循环
            }
            // 定义动画
            @keyframes move {
                0%{
                    transform: scale(1);
                    filter: blur(10px);
                    -webkit-filter: blur(10px);
                }
                100%{
                    transform: scale(1.5);
                    filter: blur(0);
                    -webkit-filter: blur(0);
                }
            }
        }
        .category_bj::after {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-color: #000;
            opacity: 0.3;
        }
        .archive-title, .total {
            color: #f3f3f3;
            font-size: 40px;
            text-shadow: rgba(14, 14, 37, 0.87) -2px 2px 10px;
            position: absolute;
            top: 20%;
            left: 50%;
            transform: translate(-50%, -50%);
        }
        .total {
            top: 50%;
            font-size: 100px;
            opacity: .3;
        }
        .category_class {
            position: absolute;
            bottom: -80px;
            left: 50%;
            transform: translate(-50%);
            width: 1200px;
            height: 160px;
            background-color: #34425a;
            border-radius: 10px;
            box-shadow: 0px 12px 8px -12px #000;
            .filter-item {
                height: 26px;
                margin-top: 20px;
                overflow: hidden;
                color: #f3f3f3;
                text-shadow: rgba(14, 14, 37, 0.87) -2px 2px 10px;
                .filter-tag li, .g-class-top {
                    display: inline-block;
                    padding: 1px 10px;
                    border: 1px dashed #a5a5a5;
                    border-radius: 4px;
                    box-shadow: 0 3px 1px -2px rgba(0, 0, 0, 0.2);
                    background-color: rgba(0, 0, 0, 0);
                    margin-right: 10px;
                    cursor: pointer;
                }
                .filter-tag .g-class-top {
                    display: inline-block;
                    border: 1px solid transparent;
                    background-color: rgba(0, 0, 0, 0);
                    margin-left: 10px;
                    color: #fff;
                }
                .g-class-top span {
                    margin-left: 5px;
                }
                .filter-tag li:hover {
                    color: #fff;
                    background-color: #fb7299;
                    border: 1px solid transparent;
                }
            }
        }
    }
    .category_list {
        padding-top: 100px;
    }
}
// 分页样式
.pager-box /deep/ .el-pagination {
    padding: 20px 0;
    .el-pagination__total {
        padding: 0 14px;
        min-width: 35.5px;
        color: #99a9bf;
        border: 1px solid #34425a;
    }
    .el-pagination__total,
    .btn-prev,
    .btn-next,
    .number,
    .el-input--mini,
    .el-input__inner,
    .el-icon {
        height: 39px;
        line-height: 39px;
        border-radius: 3px !important;
        font-size: 14px;
        font-weight: 700;
        // background: #34425a !important;
        box-shadow: 0 3px 1px -2px rgba(0, 0, 0, 0.2);
    }
    .el-input--mini .el-input__inner {
        font-size: 15px;
        background-color: rgba(0, 0, 0, 0);
        color: #99a9bf;
        border: 1px solid #34425a;
    }
    button span {
        font-size: 16px !important;
        width: 68px;
        height: 39px;
        line-height: 39px;
        font-weight: 700;
    }
    .number {
        width: 39px;
    }
    .number:hover,
    .el-icon:hover {
        // background-color: #fb7299 !important;
        color: #fff !important;
        transition: all 0.5s;
    }
}
// 分页选中状态背景颜色
.pager-box
    /deep/
    .el-pagination.is-background
    .el-pager
    li:not(.disabled).active {
    background-color: #fb7299 !important;
}
.pager-box /deep/ .el-pagination.is-background .el-pager li {
    margin: 0 5px;
    background-color: #34425a;
    color: #99a9bf;
}
// 上下页状态背景
.pager-box /deep/ .el-pagination.is-background .btn-prev {
    background-color: #34425a;
    color: #fff;
}
.pager-box /deep/ .el-pagination.is-background .btn-next {
    background-color: #34425a;
    color: #fff;
}
.pager-box /deep/ .el-pagination.is-background .btn-prev:disabled {
    background-color: #34425a;
    color: #99a9bf;
    opacity: 0.5;
}
.pager-box /deep/ .el-pagination.is-background .btn-next:disabled {
    background-color: #34425a;
    color: #99a9bf;
    opacity: 0.5;
}
</style>