<template>
    <!-- 登录 -->
    <div class="user-register">
        <!-- <div class="el-tabs__header"></div> -->
        <!-- 消息提示 -->
        <transition name="el-zoom-in-center">
            <el-alert
                v-if="flag"
                :title="meaasge"
                :type="type"
                center
                show-icon
                :closable="false"
            >
            </el-alert>
        </transition>
        <!-- 表单区域 -->
        <div class="form-login">
            <!-- 登录名称 -->
            <div class="form-group form-inline">
                <!-- <label for="username">账号</label> -->
                <input
                    type="text"
                    class="form-control"
                    placeholder="请输入用户名"
                    v-model="username"
                />
            </div>
            <!-- 手机号 -->
            <!-- <div class="form-group form-inline">
                <input
                    type="text"
                    class="form-control"
                    placeholder="手机号（选填）"
                    v-model="phone"
                />
            </div> -->
            <!-- 邮箱 -->
            <!-- <div class="form-group form-inline">
                <input
                    type="text"
                    class="form-control"
                    placeholder="邮箱账号（选填）"
                    v-model="email"
                />
            </div> -->
            <!-- 登录密码 -->
            <div class="form-group form-inline">
                <!-- <label for="password">密码</label> -->
                <!-- 通过 ref 引用 获取用户密码 -->
                <input
                    type="password"
                    class="form-control"
                    placeholder="请输入密码"
                    v-model="password"
                />
            </div>
            <!-- 邀请码 -->
            <div class="form-group form-inline">
                <!-- <label for="password">邀请码</label> -->
                <!-- 通过 ref 引用 获取用户密码 -->
                <input
                    type="text"
                    class="form-control"
                    placeholder="邀请码(选填)"
                    v-model="invite"
                />
            </div>
            <!-- 登录 -->
            <button type="button" class="btn-primary" @click="btn_Register">
                注册
            </button>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            activeName: "first",
            // 注册
            username: "",
            password: "",
            // phone: "",
            // email: "",
            invite: "", // 邀请码
            // 消息提示
            meaasge: "",
            type: "",
            flag: false,
        };
    },
    methods: {
        // 用户注册
        btn_Register() {
            this.$http.user
                .register({
                    username: this.username,
                    // phone: this.phone,
                    // email: this.email,
                    password: this.password,
                    refcode: this.invite,
                    flag: 1,
                })
                .then(({ code, msg }) => {
                    if (code === 200) {
                        // 注册成功
                        this.type = "success"
                        this.flag = true;
                        this.meaasge = "注册成功，请登录您的账户";
                    } else {
                        // 消息提示
                        this.type = "error"
                        this.flag = true;
                        this.meaasge = msg;
                    }
                });
        },
    },
};
</script>

<style lang="less" scoped>
.el-tabs__header {
    width: 100px;
    height: 100px;
    background: url("../../../assets/avatar.png");
    background-size: cover;
    border-radius: 50%;
    margin: 20px auto;
    // margin-bottom: 20px;
}
.form-login {
    width: 500px;
    height: 100%;
    // background-color: green;
    margin: 0 auto;
    .form-group {
        width: 100%;
        height: 40px;
        padding-top: 20px;
        display: flex;
        label {
            flex: 1;
            line-height: 40px;
            color: #fff;
        }
        .form-control {
            flex: 9;
            outline: 0;
            border-radius: 4px;
            border: 1px solid #dcdfe6;
            line-height: 40px;
            padding: 0 15px;
            font-size: 16px;
            font-weight: 700;
            font-family: "PingFang SC", "Lantinghei SC", "Microsoft YaHei",
                "Helvetica Neue", "Open Sans", Arial, "Hiragino Sans GB",
                sans-serif;
        }
    }
    .btn-primary {
        width: 100%;
        height: 40px;
        margin: 35px 0;
        border-radius: 4px;
        outline: none;
        border: 0;
        background-color: #409eff;
        opacity: 1;
        font-size: 16px;
        color: #fff;
        cursor: pointer;
        font-family: "PingFang SC", "Lantinghei SC", "Microsoft YaHei",
            "Helvetica Neue", "Open Sans", Arial, "Hiragino Sans GB", sans-serif;
        transition: all 0.5s;
    }
    .btn-primary:hover {
        opacity: 0.8;
    }
    
}
</style>