<template>
    <div class="mnlist w">
        <!-- Tab 切换栏：分类切换 -->
        <div class="sort_wrap">
            <ul class="sort_list">
                <li v-for="(item, index) in dts" :key="index" @click="change(index)">
                    <a :class="{ active: index == current }">{{ item.name }}</a>
                </li>
            </ul>
        </div>
        <!-- 明星列表 -->
        <MnLib :mnData="mnData"></MnLib>
        <!-- 分页功能：使用 ElementUi -->
        <!-- <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="pagenum"
            :page-sizes="[30, 42, 60, 90]"
            :page-size="pagesize"
            :total="total"
            layout="total, sizes, prev, pager, next, jumper"
            hide-on-single-page= "true"
            background>
        </el-pagination> -->
    </div>
</template>

<script>
// 导入组件
import MnLib from "@/components/Meinv/MnLib.vue"
// 导入自封装的小axios
// import request from "@/utils/request.js"

export default {
    // 注册组件
    components: {
        MnLib
    },
    data() {
        return {
            // 用于tab栏选中状态 和 传值给子组件(切换明星列表)
            current: 0,
            // Tab 栏分类
            dts: [],
            mnData: [], // 用于转存明星信息
            // 分页功能
            // userList: [],
            // pagenum: 1, // 页数
            // pagesize: 30, // 每页展示多少条数据
            // total: 70, // 总条数
        }
    },
    methods: {
        // Tab 栏选中状态变色
        change(index) {
            this.current = index
            // 把 索引的值 临时存储到浏览器本地（刷新浏览器让选中状态不消失）
            sessionStorage.setItem("mnListTab", this.current)
        },
        // 发起 axios 请求：获取 明星列表数据 的方法
        // async getUserList(mnListUrl) {
        //     const {data: res} = await request.get(mnListUrl)
        //     this.mnData = res.mnList
        // },
        
        //-------------------分页功能-------------------
        // 获取数据列表
        // async getUserList() {
        //     const {data: res} = await request.get("/articles", {
        //         params: {_page: this.pagenum, _limit: this.pagesize}
        //     })
        //     this.userList = res
        // },
        // // pageSize 改变时会触发
        // handleSizeChange(newSize) {
        //     // console.log(`每页 ${newSize} 条`);
        //     this.pagesize = newSize
        //     this.getUserList()
            
        // },
        // // currentPage 改变时会触发
        // handleCurrentChange(newPage) {
        //     // console.log(`当前页: ${newPage}`);
        //     this.pagenum = newPage
        //     this.getUserList()
        // },
    },
    created() {
        // 请求明星分类
        this.$http.video.categoryStar().then(({data})=> {
            this.dts = data
        })
    },
    mounted() {
        // 把 临时存储在浏览器本地的 索引值 赋值给current
        this.current = sessionStorage.getItem("mnListTab")
        // 解决首次进入页面时没有选中状态
        if (this.current == null) {
            this.current = 0
        }
    },
    // 侦听器：监听 current 值的变化,从而请求到不同的数据
    watch: {
        // 对象格式的侦听器：监听 current 值的变化
        current: {
            // handler 是固定写法,表示当 current 的值发生变化,自动调用 handler 处理函数
            handler(newVal) {
                // 请求明星列表数据
                this.$http.video.categoryStar().then(({data})=> {
                    this.mnData = data[newVal].children
                })
            },
            immediate: true // 进入页面时自动触发一次
        },
    },
}
</script>

<style lang="less" scoped>
.sort_wrap {
    width: 100%;
    height: 40px;
    margin: 15px 0;
    .sort_list li {
        display: inline-block;
        a {
            display: inline-block;
            margin-right: 10px;
            padding: 0 20px;
            height: 40px;
            border-radius: 2px;
            background-color: #34425a;
            color: #99a9bf;
            line-height: 40px;
            box-shadow: 0 10px 13px rgba(0,0,0,0.1);
            cursor: pointer;
        }
    }
}
// 待添加类
.active {
    color: #fff !important;
    background-color: #fb7299 !important;
}
</style>