<template>
    <div class="classify-topic w">
        <!-- Tab 切换栏：分类切换 -->
        <div class="sort_wrap">
            <ul class="sort_list">
                <li
                    v-for="item in dts"
                    :key="item.id"
                    @click="change(item.id)"
                >
                    <a :class="{ active: item.id == topicID }">{{
                        item.title
                    }}</a>
                </li>
            </ul>
        </div>
        <!-- 视频列表分类 -->
        <el-row :gutter="15">
            <VideoInfo
                v-for="item in topicVideo"
                :key="item.id"
                :span="6"
                :id="item.id"
                :title="item.title"
                :pic="item.thumb"
                :longUrl="item.video_url"
                :shortUrl="item.short_video_url"
                :view="item.view_num"
                :like="item.like_num"
                :tags="item.tags"
                :time="item.mtime"
                :category="item.category_name"
                :topic_info="item.topic_info"
                :is_like="item.is_like"
            >
            </VideoInfo>
        </el-row>
        <!-- 分页功能：使用 ElementUi -->
        <div class="pager-box">
                <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="pagenum"
                :page-sizes="[1, 2, 3, 5]"
                :page-size="pagesize"
                :total="total"
                layout="total, sizes, prev, pager, next"
                prev-text="上一页"
                next-text="下一页"
                background
                style="text-align: center;"
                :hide-on-single-page="true">
            </el-pagination>
        </div>
    </div>
</template>

<script>
import axios from 'axios'
export default {
    data() {
        return {
            dts: [], // Tab 栏分类
            topicID: 1, // 分类id：用于tab栏选中状态 和 切换请求数据
            topicVideo: [],
            // 分页功能
            pagenum: 1, // 页数
            pagesize: 1, // 每页展示多少条数据
            total: 70, // 总条数
        };
    },
    methods: {
        // Tab 栏选中状态变色
        change(id) {
            this.topicID = id
            this.pagenum = 1 // 切换Tab栏时,让页码值为1
        },
        // 发起 axios 请求：获取所有的话题分类
        getCategory () {
            this.$http.video.topic().then(({ data }) => {
                this.dts = data.list
                this.topicID = data.list[0].id // 把 第一个分类的id 赋值给 topicID
            })
        },
        // 发起 axios 请求：获取 分类视频列表数据 的方法
        // getTopicVideo (mid) {
        //     this.$http.video.list({
        //         page: this.pagenum, // 页数
        //         limit: this.pagesize, // 每页条数
        //         topic_id: mid
        //     }).then(({ data }) => {
        //         this.topicVideo = data.list;
        //         this.total = data.total // 当前分类视频总条数
        //     }).catch(err => {
        //         console.log(err);
        //     })
        // },
        getTopicVideo(mid) {
            axios.all([
                this.$http.video.list({
                    page: this.pagenum, // 页数
                    limit: this.pagesize, // 每页条数
                    topic_id: mid
                }),
                this.$http.video.videoStar({
                    page: this.pagenum, // 页数
                    limit: this.pagesize, // 每页条数
                    topic_id: mid
                }),
            ]).then(axios.spread((video, videoStar)=> {
                if(video.code && videoStar.code === 200) {
                    this.topicVideo = video.data.list.concat(videoStar.data.list)
                    this.total = video.data.total + videoStar.data.total
                } else if(!video.data) {
                    return
                }
            })).catch(err => {
                console.log(err);
            })
        },
        //-------------------分页功能-------------------
        // pageSize 改变时会触发
        handleSizeChange(newSize) {
            // console.log(`每页 ${newSize} 条`);
            this.pagesize = newSize
            this.getTopicVideo(this.topicID)
        },
        // currentPage 改变时会触发
        handleCurrentChange(newPage) {
            // console.log(`当前页: ${newPage}`);
            this.pagenum = newPage
            this.getTopicVideo(this.topicID)
        },
    },
    mounted() {
        this.getCategory()
    },
    // 侦听器：监听 topicID 值的变化,从而请求到不同的数据
    watch: {
        // 对象格式的侦听器：监听 topicID 值的变化
        topicID: {
            // handler 是固定写法,表示当 topicID 的值发生变化,自动调用 handler 处理函数
            handler(newID) {
                this.topicID = newID
                // console.log("当前分类id为", newID);
                this.getTopicVideo(newID);
            },
            // immediate: true, // 进入页面时自动触发一次
        },
    },
};
</script>

<style lang="less" scoped>
.sort_wrap {
    width: 100%;
    height: 40px;
    margin: 15px 0 25px 0;
    text-align: center;
    .sort_list li {
        display: inline-block;
        a {
            display: inline-block;
            margin-right: 10px;
            padding: 0 20px;
            height: 40px;
            border-radius: 2px;
            background-color: #34425a;
            color: #99a9bf;
            line-height: 40px;
            box-shadow: 0 10px 13px rgba(0, 0, 0, 0.1);
            cursor: pointer;
        }
    }
}
// 分页样式
.pager-box /deep/ .el-pagination {
    padding: 20px 0;
    .el-pagination__total {
        padding: 0 14px;
        min-width: 35.5px;
        color: #99a9bf;
        border: 1px solid #34425a;
    }
    .el-pagination__total, .btn-prev, .btn-next, .number, .el-input--mini, .el-input__inner,.el-icon {
        height: 39px;
        line-height: 39px;
        border-radius: 3px !important;
        font-size: 14px;
        font-weight: 700;
        // background: #34425a !important;
        box-shadow: 0 3px 1px -2px rgba(0,0,0,.2);
    }
    .el-input--mini .el-input__inner {
        font-size: 15px;
        background-color: rgba(0, 0, 0, 0);
        color: #99a9bf;
        border: 1px solid #34425a;
    }
    button span {
        font-size: 16px !important;
        width: 68px;
        height: 39px;
        line-height: 39px;
        font-weight: 700;
    }
    .number {
        width: 39px;
    }
    .number:hover, .el-icon:hover {
        // background-color: #fb7299 !important;
        color: #fff !important;
        transition: all .5s;
    }
}
// 分页选中状态背景颜色
.pager-box /deep/ .el-pagination.is-background .el-pager li:not(.disabled).active {
  background-color: #fb7299 !important;
}
.pager-box /deep/ .el-pagination.is-background .el-pager li {
    margin: 0 5px;
    background-color: #34425a;
    color: #99a9bf;
}
// 上下页状态背景
.pager-box /deep/ .el-pagination.is-background .btn-prev {
    background-color: #34425a;
    color: #fff;
}
.pager-box /deep/ .el-pagination.is-background .btn-next {
    background-color: #34425a;
    color: #fff;
}
.pager-box /deep/ .el-pagination.is-background .btn-prev:disabled {
    background-color: #34425a;
    color:#99a9bf;
    opacity: 0.5;
}
.pager-box /deep/ .el-pagination.is-background .btn-next:disabled {
    background-color: #34425a;
    color:#99a9bf;
    opacity: 0.5;
}
// 待添加类
.active {
    color: #fff !important;
    background-color: #fb7299 !important;
}
</style>