<template>
    <div class="home-container w">
        <div class="home-slide">
            <!-- 左侧用户信息 -->
            <div class="to_login">
                <UserInfo></UserInfo>
            </div>
            <!-- 轮播 -->
            <div class="merry-go-round">
                <Swiper :swiperList="swiperMovies"></Swiper>
            </div>
            <!-- 右侧排行榜 -->
            <div class="hot_news">
                <HotNew :hotList="hotMovies"></HotNew>
            </div>
        </div>
        <!-- Tab 分栏 -->
        <TabBox></TabBox>
        <!-- 随机推荐 -->
        <RandomVideo></RandomVideo>
        <!-- 分类视频 -->
        <Topic></Topic>
    </div>
</template>

<script>
// 导入组件
import UserInfo from "@/components/Home/UserInfo.vue"
import Swiper from "@/components/Home/Swiper.vue"
import HotNew from "@/components/Home/HotNew.vue"
import TabBox from "@/components/Home/TabBox.vue"
import RandomVideo from "@/components/Home/RandomVideo.vue" // 随机视频
import Topic from "@/components/Home/Topic.vue" // 分类列表
// 导入 axios
import axios from "axios"

export default {
    name: "Home",
    // 注册组件
    components: {
        UserInfo,
        Swiper,
        HotNew,
        TabBox,
        RandomVideo,
        Topic
    },
    data() {
        return {
            // 轮播
            swiperMovies: [],
            // 人气排行
            hotMovies: [],
        }
    },
    methods: {
        // 同时发起多个请求
        getData () {
            axios.all([
                // 请求多个api：swiperVideo 是 api/video.js 中的请求方法名
                this.$http.video.swiperVideo(), // 轮播
                this.$http.video.hotList(), // 人气排行
            ]).then(axios.spread((advert, hotVideo) => {
                this.swiperMovies = advert.data // 轮播
                this.hotMovies = hotVideo.data.list // 人气
            })).catch(err => {
                console.log("请求失败", err);
            })
        },
    },
    mounted() {
        this.getData()
    }
};
</script>

<style lang="less" scoped>
.home-container {
    padding-top: 135px; // 解决被头部遮挡的问题
    .home-slide {
        position: relative;
        width: 100%;
        height: 410px;
        // border: 1px solid red;
        margin-top: 20px;
        // 左侧用户信息
        .to_login, .hot_news {
            position: absolute;
            width: 222px;
            height: 100%;
            // background-color: #28374a;
            // background: -webkit-linear-gradient(bottom,#28374a,#34425a 15px) #28374a;
            // border: 1px solid #28374a;
            box-shadow: 0 10px 13px rgba(0,0,0,0.1);
        }
        // 轮播
        .merry-go-round {
            position: absolute;
            left: 50%;
            transform: translate(-50%);
            width: 710px;
            height: 100%;
            border-radius: 7px;
            overflow: hidden;
            box-shadow: 0 10px 13px rgba(0,0,0,0.1);
        }
        // 右侧热门排行
        .hot_news {
            background-color: #34425a;
            right: 0;
        }
    }
    
}
</style>