<template>
    <div class="user-like">
        <!-- 视频列表分类 -->
        <el-row :gutter="15">
            <VideoInfo
                v-for="item in likeList"
                :key="item.id"
                :span="6"
                :id="item.id"
                :title="item.title"
                :pic="item.thumb"
                :longUrl="item.video_url"
                :shortUrl="item.short_video_url"
                :view="item.view_num"
                :like="item.like_num"
                :tags="item.tags"
                :time="item.mtime"
                :category="item.category_name"
                :topic_info="item.topic_info"
                :is_like="1"
            >
            </VideoInfo>
        </el-row>
        <!-- 分页功能：使用 ElementUi -->
        <div class="pager-box">
                <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="pagenum"
                :page-size="pagesize"
                :total="total"
                layout="total, prev, pager, next"
                prev-text="上一页"
                next-text="下一页"
                background
                style="text-align: center;"
                :hide-on-single-page="true">
            </el-pagination>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            likeList: [], // 我的点赞/喜欢
            // 分页功能
            pagenum: 1, // 页数
            pagesize: 4, // 每页展示多少条数据
            total: 70, // 总条数
        }
    },
    methods: {
        getLikeVideo() {
            this.$http.user.userLikeList({
                page: this.pagenum, // 页数
                limit: this.pagesize, // 每页条数
            }).then(({data}) => {
                if(data) {
                    this.likeList = data.list
                    this.total = data.total
                }
            })
        },
        //-------------------分页功能-------------------
        // pageSize 改变时会触发
        handleSizeChange(newSize) {
            // console.log(`每页 ${newSize} 条`);
            this.pagesize = newSize
            this.getLikeVideo()
        },
        // currentPage 改变时会触发
        handleCurrentChange(newPage) {
            // console.log(`当前页: ${newPage}`);
            this.pagenum = newPage
            this.getLikeVideo()
        },
    },
    created() {
        this.getLikeVideo()
    },

}
</script>

<style lang="less" scoped>
// 分页样式
.pager-box /deep/ .el-pagination {
    padding: 20px 0;
    .el-pagination__total {
        padding: 0 14px;
        min-width: 35.5px;
        color: #99a9bf;
        border: 1px solid #34425a;
    }
    .el-pagination__total, .btn-prev, .btn-next, .number, .el-input--mini, .el-input__inner,.el-icon {
        height: 39px;
        line-height: 39px;
        border-radius: 3px !important;
        font-size: 14px;
        font-weight: 700;
        // background: #34425a !important;
        box-shadow: 0 3px 1px -2px rgba(0,0,0,.2);
    }
    .el-input--mini .el-input__inner {
        font-size: 15px;
        background-color: rgba(0, 0, 0, 0);
        color: #99a9bf;
        border: 1px solid #34425a;
    }
    button span {
        font-size: 16px !important;
        width: 68px;
        height: 39px;
        line-height: 39px;
        font-weight: 700;
    }
    .number {
        width: 39px;
    }
    .number:hover, .el-icon:hover {
        // background-color: #fb7299 !important;
        color: #fff !important;
        transition: all .5s;
    }
}
// 分页选中状态背景颜色
.pager-box /deep/ .el-pagination.is-background .el-pager li:not(.disabled).active {
  background-color: #fb7299 !important;
}
.pager-box /deep/ .el-pagination.is-background .el-pager li {
    margin: 0 5px;
    background-color: #34425a;
    color: #fff;
}
// 上下页状态背景
.pager-box /deep/ .el-pagination.is-background .btn-prev {
    background-color: #34425a;
    color: #fff;
}
.pager-box /deep/ .el-pagination.is-background .btn-next {
    background-color: #34425a;
    color: #fff;
}
.pager-box /deep/ .el-pagination.is-background .btn-prev:disabled {
    background-color: #34425a;
    color:#99a9bf;
    opacity: 0.5;
}
.pager-box /deep/ .el-pagination.is-background .btn-next:disabled {
    background-color: #34425a;
    color:#99a9bf;
    opacity: 0.5;
}

</style>