<template>
    <div class="hot_new-container">
        <!-- 人气排行图标 -->
        <span class="hot-icon"></span>
        <!-- 人气排行 -->
        <ul class="hot-list">
            <li class="r2">
                <!-- 1. 绑定路由跳转事件,并传参：用于跳转到对应的播放页 -->
                <a v-for="item in hotList" :key="item.id" @click="toPath(item.id, item.title, item.video_url)">
                    <img :src="item.thumb" alt="">
                    <span class="bt">{{ item.title }}</span>
                </a>
            </li>
        </ul>
    </div>
</template>

<script>
export default {
    props: ["hotList"],
    name: "HotNew",
    methods: {
        // 2. 播放对应视频：路由跳转,并将接收到的参数,传入到浏览器地址栏中
        toPath(id, tit, url) {
            const { href } = this.$router.resolve({
                name: "myPlaye",
                query: { id: id },
            });
            // local存值：将多个数据存放在浏览器本地（解决刷新页面时数据丢失的问题）
            localStorage.setItem('rowData', JSON.stringify({
                src: url, title: tit
            }))
            window.open(href, '_blank');
            // this.$router.push({
            //     // params 传参 必须使用 name 属性来引入路由地址（属性值是路由模块中定义的值）
            //     name: "myPlaye",
            //     // 查询参数：相当于 get 请求,参数会在地址栏中显示
            //     query: { id: id},
            //     // 重要信息 使用 params 传参（相当于 post,不会在地址栏中显示）
            //     params: { src: url, title: tit }
            // })
        }
    }
};
</script>

<style lang="less" scoped>
.hot_new-container {
    position: relative;
    width: 100%;
    height: 100%;
    .hot-icon {
        position: absolute;
        right: -8px;
        top: -8px;
        width: 80px;
        height: 80px;
        background: url("../../assets/hot.png") no-repeat;
        background-size: 80px;
        z-index: 99;
    }
    .hot-list {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 206px;
        height: 389px;
        .r2 a {
            position: relative;
            display: block;
            width: 205px;
            height: 118px;
            margin-bottom: 15px;
            border: 1px solid #28374a;
            cursor: pointer;
            overflow: hidden;
            border-radius: 7px;
            img {
                position: absolute;
                width: 100%;
                height: 100%;
                // 自适应填满盒子
                object-fit: cover;
                transition:all 0.5s;
                filter: brightness(75%); // 亮度
            }
            .bt {
                position: absolute;
                bottom: 0px;
                width: 100%;
                height: 22px;
                background-color: rgba(0, 0, 0,.5);
                color: #fff;
                text-shadow: 0 3px 5px rgba(0,0,0,.3);
                font-size: 12px;
                text-align: center;
                line-height: 22px;
                overflow: hidden;
            }
        }
        .r2 a:hover img {
            // 图片放大1.2倍
            filter: brightness(100%); // 亮度
            transform:scale(1.2);
        }
    }
}
</style>