<template>
    <div class="login-container w">
        <div class="tablist">
            <el-tabs type="border-card">
                <el-tab-pane label="登录账号">
                    <UserLogin @getCode="getNewCode"></UserLogin>
                </el-tab-pane>
                <el-tab-pane label="注册账号">
                    <UserRegister></UserRegister>
                </el-tab-pane>
            </el-tabs>
        </div>
    </div>
</template>

<script>
import UserLogin from "@/components/User/user_login/user-login.vue";
import UserRegister from '@/components/User/user_register/user-register.vue'

export default {
    components: {
        UserLogin,
        UserRegister
    },
    methods: {
        // 接收子组件的状态码
        getNewCode(code) {
            if (code === 200) {
                // 触发父组件中的自定义事件,并传值：用于关闭弹窗
                this.$emit("numchange", false);
            }
        },
    },
};
</script>

