<template>
    <div class="video_playe-container">
        <div class="left-main">
            <video-player
                class="video-player vjs-custom-skin"
                ref="videoPlayer"
                :playsinline="true"
                :options="playerOptions"
                @play="onPlayerPlay"
                @pause="onPlayerPause"
                @ended="onPlayerEnded"
            >
            </video-player>
        </div>
        <div class="left-foot">
            <h1 class="entry-title">正在播放：{{ videoName }}</h1>
            <div class="rating">
                <i class="el-icon-star-off">
                    <span class="grey-link">喜欢</span>
                </i>
                <i class="el-icon-star-off">
                    <span class="grey-link">不喜欢</span>
                </i>
                <i class="el-icon-star-off">
                    <span class="grey-link">收藏</span>
                </i>
            </div>
        </div>
    </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'

export default {
    data() {
        return {
            // 播放器配置参数
            playerOptions: {
                playbackRates: [0.7, 1.0, 1.5, 2.0], //播放速度
                autoplay: false, //如果true,浏览器准备好时开始回放。
                muted: false, // 默认情况下将会消除任何音频。
                loop: false, // 导致视频一结束就重新开始。
                preload: "auto", // 建议浏览器在<video>加载元素后是否应该开始下载视频数据。auto浏览器选择最佳行为,立即开始加载视频（如果浏览器支持）
                language: "zh-CN",
                aspectRatio: "16:9", // 将播放器置于流畅模式，并在计算播放器的动态大小时使用该值。值应该代表一个比例 - 用冒号分隔的两个数字（例如"16:9"或"4:3"）
                fluid: true, // 当true时，Video.js player将拥有流体大小。换句话说，它将按比例缩放以适应其容器。
                sources: [
                    {
                        type: "", //这里的种类支持很多种：基本视频格式、直播、流媒体等，具体可以参看git网址项目
                        src: "", //url地址
                    },
                ],
                poster: "../assets/images/211010113337-3-1200.jpg", //你的封面地址
                width: "500px", //播放器宽度
                notSupportedMessage: "此视频暂无法播放，请稍后再试", //允许覆盖Video.js无法播放媒体源时显示的默认信息。
                controlBar: {
                    timeDivider: true,
                    durationDisplay: true,
                    remainingTimeDisplay: false,
                    fullscreenToggle: true, //全屏按钮
                },
            },
            // 转存路由对象中的信息(包含名称,点赞数等)
            videoName: "",
            videoURL: "",
            mid: "",
        };
    },
    methods: {
        // 播放事件：开始播放时执行
        onPlayerPlay(e) {
            // console.log("开始播放");
        },
        // 暂停事件：暂停播放时执行
        onPlayerPause() {
            // console.log("暂停播放");
        },
        // 播放结束：结束播放时执行
        onPlayerEnded() {
            // console.log("播放结束");
        },
        // 播放对应视频：获取路由地址中的信息(包含视频标题)
        getPathInfo() {
            // console.log(this.$route);
            // 获取 query 传递过来的值(查询参数的值)，地址栏中是显示的
            this.mid = this.$route.query.id;
            // 获取 params 传递过来的值，地址栏中是隐藏的（刷新浏览器会丢失数据）
            // this.videoName = this.$route.params.title; // 标题
            // this.playerOptions.sources[0].src = this.$route.params.src; // 视频地址

            // 取值：获取浏览器本地中的数据（解决刷新页面数据丢失的问题）
            let dataObj = JSON.parse(localStorage.getItem("rowData"));
            this.videoName = dataObj.title; // 标题
            this.playerOptions.sources[0].src = dataObj.src; // 视频地址
        },
        // 将 store 中的 actions 中的方法,映射为该组件的方法
        ...mapActions(["getUser"]),
    },
    // 计算属性
    computed: {
        // 将 store 中的 getters 中方法的返回值,映射为该组件的计算属性
        ...mapGetters(['numberPlays']),
        player() {
            return this.$refs.videoPlayer.player
        }
    },
    mounted() {
        this.player.on('play', () => {
            if (this.numberPlays < 1) {
                console.log("免费观看次数已用完");
                this.player.pause() // 暂停
            } else {
                this.$http.user.viewVideo() // 扣除观看次数
                console.log(this.numberPlays);
            }
        })
        // 扣除观看次数
        // if(this.numberPlays < 1) {
        //     this.player.pause() // 暂停
        //     console.log("免费次数已经用完，请开通会员或者推广获取更多免费观看次数");
        // } else {
        //     this.$http.user.viewVideo() // 扣除观看次数
        //     this.getUser()
        //     console.log(this.numberPlays);
        // }
    },
    created() {
        this.getPathInfo();
        // 向父组件传递值
        this.$emit("movieName", this.videoName)
    },
};
</script>

<style lang="less" scoped>
.left-main {
    position: absolute;
    left: 0;
    width: 900px;
    height: 560px;
}
// 底部
.left-foot {
    position: absolute;
    bottom: 0;
    width: 894px;
    height: 94px;
    border-left: 6px solid #ff3565;
    background-color: #34425a;
    box-shadow: 0 2px 3px rgba(0, 0, 0, 0.3);
    .entry-title {
        display: block;
        width: 870px;
        height: 47px;
        margin-left: 12px;
        color: #eee;
        font-size: 24px;
        line-height: 47px;
        text-shadow: 0 1px 1px rgba(0, 0, 0, 0.2);
        border-bottom: 1px solid #151515;
        overflow: hidden;
    }
    .rating {
        height: 46px;
        width: 100%;
        margin-left: 12px;
        color: #eee;
        overflow: hidden;
        // background-color: orange;
        i {
            line-height: 46px;
            font-size: 24px;
            margin-right: 30px;
            .grey-link {
                margin-left: 10px;
                color: #777;
                font-size: 16px;
                line-height: 44px;
            }
        }
    }
}
</style>