import Vue from 'vue'
import VueRouter from 'vue-router'
// 导入组件
import Home from '@/views/Home.vue'
import Meinv from '@/views/Meinv.vue'
import Lib from "@/views/Lib.vue"
import Playe from "@/views/playe.vue"
import Category from "@/views/Category.vue"
// import Search from "@/views/Search.vue"

Vue.use(VueRouter)

const routes = [
  // meta 用于给页面添加 title
  { path: "/", component: Home, meta: { title: "首页" } },
  { path: "/meinv", component: Meinv, meta: { title: "明星大全" } },
  { path: "/lib/:id", component: Lib, name: "myLib" },
  // params 传参 必须使用 name 属性值为字符串
  { path: "/playe", component: Playe, name: "myPlaye", meta: { title: "视频播放" }, props: true },
  { path: "/category", component: Category, name: "myCategory", meta: { title: "所有分类" } },
  // { path: "/search", component: Search, name: "mySearch" },
]

const router = new VueRouter({
  routes
})

export default router
