<template>
    <div class="header-container">
        <!-- 迎新春 -->
        <Spring></Spring>
        <!-- 广告区域 -->
        <transition name = "fade">
        <div class="cloud" v-show = "flag">
            <div class="cloudcon w">
                <!-- logo -->
                <div class="logo"></div>
                <!-- 广告 -->
                <div class="add">
                    <img src="../assets/font.png" alt="" />
                </div>
                <!-- 加入会员 -->
                <div class="buy-vip">
                    <!-- <a @click="$router.push('/login')" class="openVip">开通VIP</a> -->
                    <!-- 登录：ElementUi 的 Dialog对话框 -->
                    <div @click="to_login = true" class="menber_r">
                        <el-button type="text" class="btn_login">
                            <img v-if="user" :src="user.avatar ? user.avatar : userIcon" />
                            <img v-else src="../assets/user.png" alt="">
                        </el-button>
                        <span class="text_login">
                            {{ user ? user.nickname || user.username: "登录/注册" }}
                        </span>
                    </div>
                    <el-dialog
                        :destroy-on-close = "true"
                        :visible.sync="to_login"
                        width="1250px"
                        center>
                        <User v-if="user"></User>
                        <!-- 自定义事件：用于接收子组件传过来的值（关闭弹窗） -->
                        <Login v-else @numchange="getNewCount"></Login>
                    </el-dialog>
                </div>
            </div>
        </div>
        </transition>
        <!-- TabBar 导航区域 -->
        <div class="tabbar">
            <div class="secondaryHeader w">
                <!-- 导航栏 -->
                <ul class="menu-wrapp">
                    <li v-for="(item, index) in menucat" :key="item.id">
                        <!-- 当index等于current时,添加active类名 -->
                        <a
                            :href="item.path"
                            @click="addClass(index)" 
                            :class="{ active: index==current}">
                            {{ item.title }}
                        </a>
                    </li>
                </ul>
                <!-- 弹窗搜索：ElementUi 的 Dialog对话框 -->
                <el-button type="text" @click="centerDialogVisible = true" class="el-icon-search"></el-button>
                <!-- destroy-on-close 关闭搜索栏时清空搜索数据 -->
                <el-dialog
                    :destroy-on-close = "true"
                    :visible.sync="centerDialogVisible"
                    width="1250px"
                    center>
                    <Search></Search>
                </el-dialog>
            </div>
        </div>
    </div>
</template>

<script>
// mapState作用：可以辅助获取到多个state的值
import { mapState } from 'vuex'
import Information from "@/components/User/user-information.vue"
import Login from '@/views/Login.vue'
import Spring from '@/components/Spring.vue'

export default {
    name: "Header",
    components: {
        User: Information,
        Login,
        Spring,
    },
    data() {
        return {
            centerDialogVisible: false, // 控制搜索弹窗是否关闭
            to_login: false, // 控制登录弹窗是否关闭
            scroll: '',
            // 控制广告区域的显示状态
            flag: true,
            // 切换导航栏时,用于匹配索引
            current: 0,
            // 导航栏分类
            menucat: [
                // 注意：a 链接路由必须加#
                { title: "本站首页", path: "#/" },
                { title: "明星大全", path: "#/meinv" },
                { title: "精选专辑", path: "#/category" },
                { title: "最新视频", path: "#/new" },
                { title: "今日推荐", path: "#/tuijian" },
                { title: "热点视频", path: "#/hot" },
                { title: "限时免费", path: "#/free" },
                { title: "图片专辑", path: "#/photo" },
            ],
            userIcon: "https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png",
        }
    },
    mounted() {
        //  监听滚动事件
        window.addEventListener('scroll', this.menu)
        // 把 临时存储在浏览器本地的 索引值 赋值给current
        this.current = sessionStorage.getItem("current")
        // 解决首次进入页面时没有选中状态
        if (this.current == null) {
            this.current = 0
        }
    },
    methods: {
        menu() {
            //  滚动位置
            this.scroll = document.documentElement.scrollTop || document.body.scrollTop;
            if (this.scroll >= 300) {
                this.flag = false
            } else {
                this.flag = true
            }
        },
        // 切换导航栏：用于点击哪个导航栏,让哪个变色(添加类)
        addClass(index) {
            // index 表示当前索引
            this.current = index
            // 把 索引的值 临时存储到浏览器本地（刷新浏览器让选中状态不消失）
            sessionStorage.setItem("current", this.current)
            // 让 路由地址 对应的 导航栏 处于选中状态（未完待续）
            // console.log(this.$route.path);
        },
        // 获取子组件传递过来的数据：用于登录成功后关闭弹窗
		getNewCount(val) {
            // 转存接收到的数据
            this.to_login = val
		},
    },
    computed: {
        // mapState 获取的值 可以直接渲染（相当于$store.state.user）
        // user 为 store/index.js 中 state 中的属性名
        ...mapState(['user'])
    },
};
</script>

<style lang="less" scoped>
// 切换导航栏时,待添加的类
.active {
    background-color: #d54d3f;
}
// 过渡效果
.fade-enter-active, .fade-leave-active {
  transition: opacity .5s;
}
.fade-enter, .fade-leave-to {
  opacity: 0;
}
// Header 样式
.header-container {
    position: fixed;
    width: 100%;
    height: auto;
    background-color: #28374a;
    z-index: 9999;
    // 广告区域
    .cloud {
        height: 80px;
        margin-bottom: 5px;
        .cloudcon {
            height: 100%;
            box-shadow:0px 12px 8px -12px #000;
            .logo {
                display: inline-block;
                width: 260px;
                height: 100%;
                padding-left: 50px;
                background: url("../assets/logo.png") no-repeat;
            }
            .add {
                display: inline-block;
            }
            .buy-vip {
                float: right;
                width: 304px;
                height: 100%;
                .menber_r {
                    width: 160px;
                    height: 40px;
                    float: right;
                    margin-top: 22px;
                    .btn_login {
                        float: left;
                        width: 40px;
                        height: 40px;
                        border: 2px solid #ccc;
                        margin: 0;
                        overflow: hidden;
                        img {
                            width: 40px;
                            height: 40px;
                            object-fit: cover;
                        }
                    }
                    .text_login {
                        display: inline-block;
                        width: 110px;
                        height: 40px;
                        margin-left: 10px;
                        line-height: 40px;
                        color: #f3f3f3;
                        text-shadow: 0px 0px 6px rgba(0, 0, 0, 0.6);
                        cursor: pointer;
                        overflow: hidden;
                        text-overflow:ellipsis;
                        white-space: nowrap;
                    }
                }
            }
        }
    }
    // TabBar 导航区域
    .tabbar {
        width: 100%;
        height: 50px;
        background-color: #28374a;
        box-shadow: 0 2px 3px rgba(0,0,0,.3);
        .secondaryHeader {
            display: flex;
            height: 100%;
            overflow: hidden;
            .menu-wrapp {
                flex: 9;
                li, a {
                    display: inline-block;
                }
                li {
                    border-right: 1px solid #34425a;
                    a {
                        height: 50px;
                        color: #f3f3f3;
                        font-size: 18px;
                        line-height: 50px;
                        padding: 0 25px;
                        text-shadow: 0 1px 1px rgba(0,0,0,.2);
                    }
                    a:hover {
                        background-color: #34425a;
                    }
                }
            }
            .menu-wrapp li:last-child {
                    border-right: 0;
                }
        }
    }
    // 弹窗搜索样式
    /deep/.el-dialog, .el-pager li {
        background: #28374a;
    }
    /deep/ .el-button {
        width: 32px;
        height: 32px;
        background-color: #445573;
        border-radius: 32px;
        box-shadow: 0 10px 13px rgba(0,0,0,.1);
        color: #fff;
        font-size: 16px;
        font-weight: bold;
        padding: 0;
        margin-top: 9px;
    }
    /deep/ .el-dialog__headerbtn {
        width: 30px;
        height: 30px;
        border-radius: 30px;
        background-color: #99a9bf;
    }
    /deep/ .el-dialog__headerbtn .el-dialog__close {
        color: #fff;
    }
    /deep/ .el-dialog__wrapper {
        backdrop-filter: blur(2px) brightness(50%);
        -moz-backdrop-filter: blur(2px) brightness(50%);
        -webkit-backdrop-filter: blur(2px) brightness(50%);
    }
    /deep/ .el-dialog__header,.el-dialog__body {
        padding: 0;
    }
}
</style>