<template>
    <!-- 视频相关的盒子样式 -->
    <div class="videoInfo">
        <!-- 采用了 ElementUI 的 Layout 分栏布局 -->
        <!-- span表示一共24份,每个盒子占8份 -->
        <el-col :span="span">
            <div class="grid-content">
                <!-- 图片 -->
                <a
                    class="list-img"
                    @click="toPath(id, title, longUrl)"
                    @mouseover.self="change1"
                    @mouseout.self="change2"
                >
                    <img :src="pic" alt="" />
                </a>
                <!-- 像素标签：当 category 为真(有内容)时,则添加label类 -->
                <span :class="{ label: category }">{{ category }}</span>
                <!-- 点赞 -->
                <i id="btn_like" 
                    v-if="$store.state.user"
                    :class="isLike === 0 ? noLike : yesLike" 
                    @click="_like(id)">
                </i>
                <!-- 信息 -->
                <div class="list-featured-tag">
                    <!-- 分类标签 -->
                    <div class="outline-tag">
                        <span
                            class="badge"
                            v-for="tag in tagsArr"
                            :key="tag"
                            :style="{
                                color: TagColor,
                                border: '1px solid' + TagColor,
                            }"
                        >
                            {{ tag ? tag : "暂无标签" }}
                        </span>
                    </div>
                    <!-- 标题 -->
                    <p class="list-title">{{ title }}</p>
                    <!-- 时间、收藏数 -->
                    <div class="list-footer">
                        <div class="time">
                            <!-- ElementUI库图标 -->
                            <i class="el-icon-time">&nbsp;{{ time }}</i>
                        </div>
                        <div class="star">
                            <!-- ElementUI库图标 -->
                            <i class="el-icon-view">&nbsp;{{ view }}</i>
                            <i class="el-icon-star-off">&nbsp;{{ likeNum }}</i>
                        </div>
                    </div>
                </div>
            </div>
        </el-col>
    </div>
</template>

<script>
export default {
    // 接收父组件传递过来的数据
    props: {
        // 分配盒子大小
        span: {
            default:"",
            type: [Number, String]
        },
        // id
        id: {
            default: "",
            type: Number,
        },
        // 视频标题
        title: {
            default: "", // 初始值
            type: String, // 值类型
        },
        // 封面图片
        pic: {
            default: "",
            type: String,
        },
        // 长视频链接
        longUrl: {
            default: 0,
            type: String, // 多个可能的类型
        },
        // 短视频链接
        shortUrl: {
            default: "",
            type: String,
        },
        // 查看数
        view: {
            default: "",
            type: Number,
        },
        // 点赞数
        like: {
            default: "",
            type: Number,
        },
        // 标签
        tags: {
            default: "",
            type: String,
        },
        // 时间
        time: {
            default: "",
            type: String,
        },
        // 视频分类
        category: {
            default: "",
            type: String,
        },
        // 信息对象
        topic_info: {
            default: {},
            type: Object,
        },
        // 是否点赞
        is_like: {
            default: 0, // 0:未点赞,1:已点赞
            type: Number
        }
    },
    data() {
        return {
            // 分类标签颜色
            TagColor: "#F66",
            // 将 props 中的标签 分割成字符串数组
            tagsArr: this.tags.split(","),
            // 点赞
            yesLike: "el-icon-remove-outline",
            noLike: "el-icon-circle-plus",
            isLike: this.is_like, // 是否点赞
            likeNum: this.like, // 点赞数
        };
    },
    methods: {
        // 鼠标经过时,改变父元素的样式
        change1(e) {
            // 给父元素 添加 change 类名
            e.target.parentNode.className = "grid-content change";
        },
        // 鼠标离开时,改变父元素的样式
        change2(e) {
            // 给父元素 移除 change 类名
            e.target.parentNode.className = "grid-content";
        },
        // 播放对应视频：路由跳转,并将接收到的参数,传入到浏览器地址栏中
        toPath(id, tit, url) {
            const { href } = this.$router.resolve({
                name: "myPlaye",
                query: { id: id },
            });
            // local存值：将多个数据存放在浏览器本地（解决刷新页面时数据丢失的问题）
            localStorage.setItem(
                "rowData",
                JSON.stringify({
                    src: url,
                    title: tit,
                })
            );
            window.open(href, "_blank");
            // this.$router.push({
            //     // params 传参 必须使用 name 属性来引入路由地址（属性值是路由模块中定义的值）
            //     name: "myPlaye",
            //     // 查询参数：使用 query 传参（相当于 get 请求,参数会在地址栏中显示）
            //     query: { id: id},
            //     // 重要信息 使用 params 传参（相当于 post,不会在地址栏中显示）
            //     params: { src: url, title: tit }
            // })
        },
        // 视频点赞
        _like(id) {
            if(this.isLike === 0) {
                this.$http.user.like_on({ vid: id }).then(({ code }) => {
                    if (code === 200) {
                        this.isLike = 1
                        this.likeNum += 1 // 点赞数
                    }
                })
            } else {
                this.$http.user.like_off({ vid: id }).then(({ code }) => {
                    if (code === 200) {
                        this.isLike = 0
                        this.likeNum -= 1 // 点赞数
                    }
                })
            }
        }
    },
};
</script>

<style lang="less" scoped>
// 采用了 ElementUI 的 Layout 分栏布局
.el-row {
    margin-top: 20px;
    &:last-child {
        margin-bottom: 0;
    }
}
.el-col {
    margin-bottom: 15px;
    .grid-content {
        position: relative;
        height: 310px;
        border-radius: 10px;
        // background-color: #fff;
        background-color: #34425a; // 暗夜主题
        box-shadow: 0 8px 6px rgba(0, 0, 0, 0.05);
        overflow: hidden;
        // 图片
        .list-img {
            position: relative;
            // display: block;
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 195px;
            overflow: hidden;
            cursor: pointer;
            img {
                width: 100%;
                height: 100%;
                // 自适应填满盒子
                object-fit: cover;
                transition: all 0.5s ease-out;
            }
        }
        // 像素标签：当列表数据category属性有内容时,添加这个类
        .label {
            position: absolute;
            top: 12px;
            left: 12px;
            padding: 3px 10px;
            border-radius: 2px;
            color: #fff;
            font-size: 12px;
            font-family: OPPOSans2;
            font-weight: 400;
            background: #f2994a;
            background: -o-linear-gradient(left, #ffc022, #f2994a);
            background: -webkit-gradient(
                linear,
                left top,
                right top,
                from(#ffc022),
                to(#f2994a)
            );
            background: linear-gradient(to right, #ffc022, #f2994a);
            z-index: 1;
        }
        // 点赞
        #btn_like {
            position: absolute;
            top: 12px;
            right: 12px;
            color: rgba(255, 255, 255, 0.8);
            font-size: 30px;
            z-index: 1;
            cursor: pointer;
            background: rgba(0, 0, 0, 0.1);
            border-radius: 50%;
        }
        // 图片遮罩
        .list-img::after {
            content: "";
            position: absolute;
            left: 0;
            width: 100%;
            height: 100%;
            background-color: #000;
            // 全透明
            opacity: 0.25;
            transition: all 0.5s;
        }
        // 当鼠标经过时
        .list-img:hover::after {
            // 遮罩层半透明
            opacity: 0;
        }
        .list-img:hover img {
            // 图片放大1.2倍
            transform: scale(1.2);
        }
        // 分类标签
        .list-featured-tag {
            width: calc(100% - 24px);
            height: 91px;
            padding: 12px;
            position: absolute;
            bottom: 0;
            // font-family:OPPOSans2;
            .outline-tag {
                width: 100%;
                height: 25px;
                overflow: hidden;
                .badge {
                    display: inline-block;
                    height: 14px;
                    font-size: 12px;
                    // 以动态颜色为主
                    color: #f66;
                    border: 1px solid #f66;
                    padding: 4px 10px;
                    margin-right: 5px;
                    margin-top: 1px;
                    border-radius: 3px;
                }
            }
            // 标题
            .list-title {
                display: block;
                height: 24px;
                line-height: 24px;
                // color: #333;
                color: rgb(153, 169, 191); // 暗夜主题
                font-size: 14px;
                margin-top: 10px;
                overflow: hidden;
                text-overflow: ellipsis; //溢出用省略号显示
                white-space: nowrap; //溢出不换行
            }
            // 时间、收藏数
            .list-footer {
                height: 24px;
                // color: #333;
                color: rgb(153, 169, 191); // 暗夜主题
                margin-top: 15px;
                font-size: 12px;
                line-height: 24px;
                .time {
                    float: left;
                    width: 120px;
                    overflow: hidden;
                }
                .star {
                    float: right;
                    i {
                        padding-left: 10px;
                    }
                }
            }
        }
    }
    // 鼠标经过时,待添加的类
    .change {
        background-color: rgba(0, 0, 0, 0.05);
        box-shadow: 0px 12px 8px -12px #000;
        transition: all 0.5s;
    }
}
</style>