// 导入配置好的 axios
import axios from '@/http/http.js'

// 2. 封装一系列 请求视频相关 数据的方法
const videoApi = {
    // 首页轮播
    swiperVideo(data) {
        return axios.post('/advert/list', { ...data, type: 2 })
    },
    // 首页人气排行
    hotList(data) {
        return axios.post('/video/list', { ...data, order: 2, limit: 3 })
    },
    // 首页随机视频
    randomVideo(data) {
        return axios.post('/video/referList', { ...data, limit: 6 })
    },
    // 首页话题分类
    topic(data) {
        return axios.post('/topic/list', data)
    },
    // 列表
    list(data) {
        return axios.post('/video/list', data)
    },
    // 一级分类
    category(data) {
        return axios.post('/category', data)
    },
    // 随机推荐
    referList(data) {
        return axios.post('/video/referList', { ...data, limit: 5 })
    },
    // 明星分类
    categoryStar(data) {
        return axios.post('/categoryStar', data)
    },
    // 明星视频
    videoStar(data) {
        return axios.post('/VideoStar/list', data)
    },


    // 点赞
    like(data) {
        return axios.post('/like/on', data)
    },
    // 取消点赞
    cancelLike(data) {
        return axios.post('/like/off', data)
    },
    // 评论列表
    commentList(data) {
        return axios.post('/comment/list', { ...data, type: 1, limit: 10 })
    },
    // 评论
    comment(data) {
        return axios.post('/comment/add', { ...data, type: 1 })
    },
    // 评论点赞
    commentLike(data) {
        return axios.post('/comment/onLike', data)
    },
    // 取消评论点赞
    cancelCommentLike(data) {
        return axios.post('/comment/offLike', data)
    },
    // 取消评论点赞
    releaseVideo(data) {
        return axios.post('/video/add', data)
    },
    // 已发布列表
    publishedList(data) {
        return axios.post('/video/me', { ...data, limit: 9 })
    },
    commentTable(data) {
        return axios.post('/comment/commentList', { ...data, limit: 10 })
    }
}

export default videoApi
