<template>
    <div class="search-container w">
        <!-- 搜索框 -->
        <div class="search">
            <!-- 按需搜索：使用了 ElementUI 的 Select 选择器 -->
            <el-select v-model="value"  :popper-append-to-body="false">
                <el-option
                    v-for="item in options"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                </el-option>
            </el-select>
            <!-- 搜索按钮 -->
            <button
                class="el-icon-search"
                @click="searchData()">
            </button>
            <!-- 输入框 -->
            <input type="text" placeholder="请输入关键词搜索" 
                v-model="inputValue"
                @focus="focus_on($event)" 
                @blur="focus_off($event)" 
                @keydown.enter="searchData()" 
            />
        </div>
        <!-- 相关内容提醒 -->
        <transition name="el-zoom-in-center">
        <el-alert 
            v-if="search_no" 
            :title="remind" 
            :type="alertType" 
            center 
            show-icon
            close-text="不再提醒">
        </el-alert>
        </transition>
        <!-- 视频列表 -->
        <el-row :gutter="15" v-if="searchResultArr.length > 0 && value == '选项1'">
            <VideoInfo
                v-for="item in searchResultArr"
                :key="item.id"
                :span="6"
                :id="item.id"
                :title="item.title"
                :pic="item.thumb"
                :longUrl="item.video_url"
                :shortUrl="item.short_video_url"
                :view="item.view_num"
                :like="item.like_num"
                :tags="item.tags"
                :time="item.mtime"
                :category="item.category_name"
                :topic_info="item.topic_info"
                :is_like="item.is_like"
            >
            </VideoInfo>
        </el-row>
        <!-- 使用 MnLib 组件,并把 搜索到的明星列表数据传给子组件,用于展示 -->
        <MnLib :mnData="searchResultArr" v-if="searchResultArr.length > 0  && value == '选项2'"></MnLib>
    </div>
</template>

<script>
// 导入组件
import MnLib from "@/components/Meinv/MnLib.vue"
// 节流
// import { throttle } from "@/utils/throttle.js";
import axios from 'axios'

export default {
    components: {
        MnLib
    },
    data() {
        return {
            inputValue:"", // 用户输入的内容(双向绑定)
            // 按需搜索
            options: [{value: '选项1',label: '电影'}, {value: '选项2',label: '明星'}],
            value: "选项1",
            // 相关内容提醒
            search_no: false,
            remind: "您可以根据【视频名称】或【明星姓名】来查询",
            alertType: "info",
            // 搜索到的内容
            searchResultArr: [],
        };
    },
    methods: {
        // 请求 视频及明星视频数据 的方法（keyword参数为:搜索关键词）
        getVideoArr(val) {
            axios.all([
                // 同时发起多个请求
                this.$http.video.list({ keyword: val }),
                this.$http.video.videoStar({ keyword: val })
            ]).then(axios.spread((video, videoStar)=> {
                // 将请求到的数据,合并成一个数组：c = a.concat(b)
                this.searchResultArr = video.data.list.concat(videoStar.data.list)
            }))
        },
        // 请求 明星数据 的方法
        getStarList(val) {
            this.$http.video.categoryStar().then(({data})=> {
                let starArr = [];
                for(var i = 0; i < data.length; i++) {
                    // 把多个数组合并成一个数组
                    starArr = starArr.concat(data[i].children)
                }
                this.searchResultArr = starArr.filter((item)=> {
                    // 判断一个数组是否包含一个指定的值
                    if(item.name.includes(val)) {
                        return item
                    }
                })
            })
        },
        searchData() {
            // 判断用户是否输入内容
            if(this.inputValue.trim()) {
                switch (this.value) {
                    case "选项1":
                        this.getVideoArr(this.inputValue.trim());
                        break;
                    case "选项2":
                        this.getStarList(this.inputValue.trim())
                        break;
                }
            } else {
                this.searchResultArr = []
                this.search_no = true
                this.alertType = "error"
                this.remind = "内容不能为空"
            }
        },
        // 获得焦点事件
        focus_on(e) {
            e.target.parentNode.style.backgroundColor = "#fff"
            e.target.style.color = "#000"
        },
        // 失去焦点事件
        focus_off(e) {
            e.target.parentNode.style.backgroundColor = "rgba(255, 255, 255, 0.15)"
            e.target.style.color = "#fff"
        },
    },
    watch: {
        value: {
            handler(newVal) {
                this.value = newVal
                this.search_no = false;
                this.inputValue = "";
                this.searchResultArr = [];
            },
            // immediate: true // 进入页面时自动触发一次
        },
        searchResultArr: {
            handler(newArr) {
                if(newArr.length <= 0 && this.inputValue.trim()) {
                    this.search_no = true
                    this.alertType = "info"
                    this.remind = "抱歉,没有查询到相关的信息"
                } else if(newArr.length > 0) {
                    this.search_no = false
                }
            }
        }
    },
};
</script>

<style lang="less" scoped>
.search-container {
    /deep/ .el-input__inner{
        width: 80px;
        height: 40px;
        border-radius: 0;
        background-color: rgba(0, 0, 0, 0);
        border: 0;
        border-right: 1px solid #99a9bf;
        color: #99a9bf;
        line-height: 40px;
        font-family: inherit;
    }
    .search {
        position: relative;
        width: 400px;
        height: 40px;
        margin: 0 auto;
        margin-bottom: 20px;
        line-height: 40px;
        border: 1px solid #fff;
        border-radius: 40px;
        background-color: rgba(rgba(255, 255, 255, 0.15));
        box-shadow: 0 13px 15px rgba(0, 0, 0, 0.15);
        transition: all .5s;
        input {
            width: 285px;
            height: 100%;
            padding-left: 10px;
            color: #fff;
            font-size: 16px;
            font-weight: 700;
            outline: none;
            border: 0;
            background-color: rgba(0, 0, 0, 0);
            font-family: inherit;
        }
        button {
            position: absolute;
            right: 3px;
            top: 50%;
            transform: translate(0, -50%);
            width: 34px;
            height: 34px;
            outline: none;
            border: 0;
            border-radius: 34px;
            background-color: #20a0ff;
            box-shadow: 0 3px 4px rgba(32,160,255,.5);
            font-size: 16px;
            font-weight: bold;
            color: #fff;
            cursor: pointer;
        }
    }
    /deep/ .el-alert {
        width: 600px;
        margin: 0 auto;
    }
}
</style>