import Vue from 'vue'
import Vuex from 'vuex' // 状态管理（类似本地存储）
import createPersistedState from 'vuex-persistedstate' // 持久化,刷新不丢失数据
import user from '@/api/user'

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        // 定义需要被全局共享的数据
        token: null,
        user: null,
        replyItem: null,
        base_config: null
    },
    mutations: {
        // 定义全局共享的方法：用于修改 state 中的数据
        setToken(state, token) {
            state.token = token
        },
        setUser(state, user) {
            state.user = user
        },
        setReplyItem(state, reply) {
            state.replyItem = reply
        },
        setBaseConfig(state, data) {
            state.base_config = data
        }
    },
    actions: {
        // 定义全局共享的异步操作方法：发起 axios 请求
        getUser(context) {
            // 发起请求：获取用户信息
            user.userInfo().then(({ code, data }) => {
                if (code === 200) {
                    // 使用 commit 函数,调用 mutations 中的setUser方法
                    // 并将 请求到的 用户信息,传给 setUser 方法
                    context.commit('setUser', data)
                }
            })
        },
        baseConfig: ({ commit }) => {
            common.config('base').then(({ code, data }) => {
                if (code === 200) {
                    commit('setBaseConfig', data)
                }
            })
        }
    },
    getters: {
        // 包装 state 中的数据（不会改变数据,只起到包装作用）
        userinfo: state => {
            return {
                truename: state.user.truename,
                qq: state.user.qq,
                avatar: state.user.avatar,
                nickname: state.user.nickname,
                desc: state.user.desc,
                sex: state.user.sex,
                birthday: state.user.birthday,
                refcode: state.user.refcode
            }
        },
        numberPlays: state => {
            return state.user ? state.user.share_free_num + state.user.vip_share_free_num : 0
        }
    },
    modules: {},
    plugins: [createPersistedState()]
})
