<template>
    <div class="container-user-information">
        <div class="user_header">
            <div class="user_avatar">
                <img :src="user.avatar || avatar" alt="" />
                <div class="user_name">
                    <p>
                        <span class="uname">{{ user.nickname || user.username }}</span>
                        <span v-if="user.nickname" class="user_id">( id: {{ user.username }} )</span>
                    </p>
                    <button @click="exit">退出登录</button>
                </div>
            </div>
            <!-- 右侧信息 -->
            <div class="info_right">
                <ul>
                    <li>
                        <p>会员到期时间</p>
                        <p>{{ user.vip_end_time || "已到期" }}</p>
                    </li>
                    <li>
                        <p>剩余观看次数</p>
                        <p>
                            <!-- {{ user.vip_share_free_num || user.share_free_num }} -->
                            {{ numberPlays }}
                        </p>
                    </li>
                    <li>
                        <p>邀请码</p>
                        <p>{{ user.refcode }}</p>
                    </li>
                    <li>
                        <p>推广数</p>
                        <p>{{ user.ref_num }}</p>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
import { mapState, mapGetters } from "vuex";

export default {
    data() {
        return {
            avatar: "https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png",
        };
    },
    methods: {
        // 退出登录
        exit() {
            this.$http.user.logout().then(({ code, msg }) => {
                if (code == 200) {
                    // 退出登录数据重置
                    this.$store.commit("setUser");
                    localStorage.removeItem("vuex"); // 清除 token 信息
                }
                // alert(msg)
            });
        },
    },
    created() {
        console.log(this.user);
    },
    computed: {
        ...mapState(["user"]),
        ...mapGetters(['numberPlays']),
    },
};
</script>

<style lang="less" scoped>
// 用户头部信息
.user_header {
    position: relative;
    width: 100%;
    height: 100px;
    background-color: rgba(0, 0, 0, 0.1);
    // box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.12), 0 0 6px 0 rgba(0, 0, 0, 0.04);
    .user_avatar {
        width: 400px;
        height: 80px;
        position: absolute;
        top: 50%;
        left: 15px;
        transform: translate(0, -50%);
        img {
            float: left;
            width: 80px;
            height: 76px;
            border: 2px solid #fff;
            border-radius: 10px;
            object-fit: cover;
        }
        .user_name {
            float: right;
            width: 300px;
            height: 100%;
            p {
                display: block;
                color: #fff;
                line-height: 36px;
                overflow: hidden;
                .uname {
                    float: left;
                    font-size: 16px;
                    max-width: 110px;
                    overflow: hidden;
                    white-space: nowrap;
                    text-overflow: ellipsis;
                }
                .user_id {
                    font-size: 14px;
                    color: #99a9bf;
                    padding-left: 5px;
                }
            }
            button {
                width: 80px;
                outline: none;
                background-color: #409eff;
                margin-top: 5px;
                border: 0;
                padding: 5px 10px;
                border-radius: 4px;
                color: #fff;
                cursor: pointer;
            }
        }
    }
    .info_right {
        width: 520px;
        height: 80px;
        // background-color: pink;
        position: absolute;
        top: 50%;
        right: 15px;
        transform: translate(0, -50%);
        ul {
            height: 100%;
            display: flex;
            align-items: center;
            li {
                flex: 1;
                height: 50px;
                margin-left: 10px;
                // background-color: orange;
                border-left: 1px solid #34425a;
                padding: 0 10px;
                overflow: hidden;
                p {
                    color: #fff;
                    padding: 4px 10px 0 10px;
                    overflow: hidden;
                    white-space: nowrap;
                }
                :first-child {
                    color: #99a9bf;
                }
            }
            :first-child {
                border: 0px;
            }
        }
    }
}
</style>