<template>
    <div class="block">
        <!-- ElementUI 组件库提供的 走马灯 -->
        <el-carousel trigger="click" :height="h" :interval="time">
            <el-carousel-item v-for="item in swiperList" :key="item.id">
                <!-- 轮播图 -->
                <img :src="item.img_url" width="100%" height="100%" alt="无图片" />
                <!-- 轮播图详情 -->
                <div class="pngfix">
                    <h4>
                        <!-- 1. 绑定路由跳转事件,并传参：用于跳转到对应的播放页 -->
                        <a @click="toPath(item.id, item.title, item.video_url)">{{ item.title }}</a>
                    </h4>
                    <p>{{ item.desc }}</p>
                </div>
            </el-carousel-item>
        </el-carousel>
    </div>
</template>

<script>
export default {
    props: ["swiperList"],
    name: "Swiper",
    data() {
        return {
            // 轮播高度
            h: "410px",
            // 轮播时间
            time: 3000,
        }
    },
    methods: {
        // 2. 播放对应视频：路由跳转,并将接收到的参数,传入到浏览器地址栏中
        toPath(id, tit, url) {
            const { href } = this.$router.resolve({
                name: "myPlaye",
                query: { id: id },
            });
            // local存值：将多个数据存放在浏览器本地（解决刷新页面时数据丢失的问题）
            localStorage.setItem('rowData', JSON.stringify({
                src: url, title: tit
            }))
            window.open(href, '_blank');
        }
    }
};
</script>

<style lang="less" scoped>
// ElementUI 组件库样式
.el-carousel__item h3 {
    color: #475669;
    font-size: 14px;
    opacity: 0.75;
    line-height: 150px;
    margin: 0;
}
// 覆盖 ElementUI 默认样式
/deep/ .el-carousel__button {
    width: 18px;
    height: 18px;
    border-radius: 100%;
    background-color: #fb7299;
}
/deep/ .el-carousel__arrow {
    background-color: rgba(251,114,153,.5);
}
/deep/ .el-carousel__arrow :hover {
    color: #fff;
}
// 轮播样式
.block {
    position: relative;
    img {
        filter: brightness(75%); // 亮度
        transition: all .5s;
    }
    :hover img {
        filter: brightness(100%); // 亮度
    }
    .pngfix {
        position: absolute;
        left: 50%;
        bottom: 50px;
        transform: translate(-50%);
        width: 640px;
        height: 135px;
        background-color: rgba(0, 0, 0, .2);
        h4 {
            margin-left: 20px;
            width: 600px;
            font-size: 24px;
            line-height: 64px;
            height: 64px;
            overflow: hidden;
            border-bottom: 1px solid #fff;
            text-align: center;
            cursor: pointer;
            a {
                color: #fff;
                text-shadow: 0 3px 5px rgba(0,0,0,.3);
            }
            :hover {
                color: #fb7299;
            }
        }
        p {
            text-shadow: 0 3px 5px rgba(0,0,0,.3);
            padding: 10px 20px 0 20px;
            font-size: 12px;
            line-height: 24px;
            color: #fff;
            height: 48px;
            overflow: hidden;
        }
    }
}
</style>