<template>
    <!-- 登录 -->
    <div class="user-seting w">
        <!-- <div class="el-tabs__header"></div> -->
        <!-- 消息提示 -->
        <transition name="el-zoom-in-center">
            <el-alert
                v-if="flag"
                :title="meaasge"
                :type="type"
                center
                show-icon
                :closable="false"
            >
            </el-alert>
        </transition>
        <!-- 表单区域 -->
        <div class="form-login">
            <!-- 原密码 -->
            <div class="form-group form-inline">
                <input
                    type="password"
                    class="form-control"
                    placeholder="请输入原来的密码"
                    v-model="old_password"
                />
            </div>
            <!-- 新的密码 -->
            <div class="form-group form-inline">
                <input
                    type="password"
                    class="form-control"
                    placeholder="请输入新的密码"
                    v-model="password"
                />
            </div>
            <!-- 确认密码 -->
            <div class="form-group form-inline">
                <input
                    type="password"
                    class="form-control"
                    placeholder="请再输入一次"
                    v-model="again_password"
                />
            </div>
            <!-- 登录 -->
            <button type="button" class="btn-primary" @click="_submit">
                确认
            </button>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            // activeName: "first",
            old_password: "",
            password: "",
            again_password: "",
            // 消息提示
            type: "",
            meaasge: "",
            flag: false,
        };
    },
    methods: {
        // 设置密码
        _submit() {
            this.$http.user
                .modification({
                    old_password: this.old_password,
                    password: this.password,
                    password_confirmation: this.again_password,
                })
                .then(({ code,msg }) => {
                    if(code === 200) {
                        this.flag = true;
                        this.type = "success"
                        this.meaasge = "修改成功,下次登录请使用新密码";
                    } else {
                        this.flag = true;
                        this.type = "error"
                        this.meaasge = msg;
                    }
                });
        },
    },
};
</script>

<style lang="less" scoped>
.user-seting {
    width: 500px;
}
.el-tabs__header {
    width: 100px;
    height: 100px;
    background: url("../../../assets/avatar.png");
    background-size: cover;
    border-radius: 50%;
    margin: 20px auto;
    // margin-bottom: 20px;
}
.form-login {
    width: 500px;
    height: 100%;
    // background-color: green;
    margin: 0 auto;
    .form-group {
        width: 100%;
        height: 40px;
        padding-top: 20px;
        display: flex;
        label {
            flex: 1;
            line-height: 40px;
            color: #fff;
        }
        .form-control {
            flex: 9;
            outline: 0;
            border-radius: 4px;
            border: 1px solid #dcdfe6;
            line-height: 40px;
            padding: 0 15px;
            font-size: 16px;
            font-weight: 700;
            font-family: "PingFang SC", "Lantinghei SC", "Microsoft YaHei",
                "Helvetica Neue", "Open Sans", Arial, "Hiragino Sans GB",
                sans-serif;
        }
    }
    .btn-primary {
        width: 100%;
        height: 40px;
        margin: 35px 0;
        border-radius: 4px;
        outline: none;
        border: 0;
        background-color: #1989fa;
        opacity: 1;
        font-size: 16px;
        color: #fff;
        cursor: pointer;
        font-family: "PingFang SC", "Lantinghei SC", "Microsoft YaHei",
            "Helvetica Neue", "Open Sans", Arial, "Hiragino Sans GB", sans-serif;
        transition: all 0.5s;
    }
    .btn-primary:hover {
        opacity: 0.8;
    }
    
}
</style>