<template>
    <div class="user-vip">
        <h1>会员充值</h1>
        <button @click="btn_buy">购买</button>
    </div>
</template>

<script>
export default {
    methods: {
        btn_buy() {
            this.$http.user.buy_vip({
                shop_id: 1,
                pay_type: "GOLD"
            }).then(({code})=> {
                if(code === 200) {
                    console.log("充值成功,请重新登录");
                } else {
                    console.log("金币不足,请充值!");
                }
            })
        }
    }
}
</script>

