<template>
    <!-- Swiper -->
    <div class="swiper-container">
        <div class="dtitle">
            <i class="el-icon-s-help"></i>
            <span>最新出道</span>
        </div>
        <div class="swiper-wrapper">
            <div class="swiper-slide" v-for="item in newMnData" :key="item.id">
                <!-- 1. 绑定路由跳转事件,并传参：用于跳转到对应的详情页 -->
                <a @click="toPath(item.id, item.name, item.icon, item.summary, item.works)">
                    <img v-if="item.icon" :src="item.icon" alt="">
                    <img v-else alt="">
                    <span class="t-text">{{ item.name }}</span>
                </a>
            </div>
        </div>
        <div class="swiper-button-prev"></div>
        <div class="swiper-button-next"></div>
        <a href="#/meinv" class="swiper-button-all">查看全部</a>
    </div>
</template>

<script>
// 导入 Swiper
import Swiper from "swiper/swiper-bundle.js"
import "swiper/swiper-bundle.css"
// (1) 导入自封装的小axios
// import request from "@/utils/request.js"

export default {
    name: "TabBox",
    
    data() {
        return {
            newMnData: [], // 用于转存明星信息
        };
    },
    methods: {
        // (2) 请求最新出道明星数据
        getUserList() {
            this.$http.video.categoryStar({ pid: 1 }).then(({data})=> {
                this.newMnData = data
            })
        },
        // 2. 打开对应明星详情页：路由跳转,并将接收到的参数,传入到浏览器地址栏中
        toPath(id, name, pic, summary, works) {
            this.$router.push({
                path: "/lib/" + id,
                // query传参：相当于 get 请求,参数会在地址栏中显示
                query: { title: name},
                // params 传参：相当于 post,不会在地址栏中显示
                // params: { pic: pic, summary: summary, works: works},
            })
            // local存值：将多个数据存放在浏览器本地（解决刷新页面时数据丢失的问题）
            localStorage.setItem('mnInfo', JSON.stringify({
                id: id, pic: pic, summary: summary, works: works
            }))
        }
    },
    mounted() {
        // 配置 swiper (轮播demo中的js代码)
        const mySwiper = new Swiper(".swiper-container", {
            loop: false, // 循环
            speed: 250, // 切换过渡(毫秒)
            slidesPerView: 6, // 展示轮播数量
            spaceBetween: 10, // 间距
            centeredSlides: false,
            watchSlidesProgress: false,
            navigation: {
                nextEl: ".swiper-button-next",
                prevEl: ".swiper-button-prev",
            },
            pagination: {
                el: ".swiper-pagination",
                clickable: true,
            },
            // 踩坑：解决 swiper 用 axios 获取轮播图片列表时,滑动及样式失效的问题
            observer:true, //修改swiper自己或子元素时，自动初始化swiper
            observeParents:true //修改swiper的父元素时，自动初始化swiper
        });
    },
    // (3) 调用 axios 请求方法
    created() {
        this.getUserList()
    },
};
</script>

<style lang="less" scoped>
.dtitle {
    width: 100%;
    height: auto;
    line-height: 50px;
    // border: 1px solid #e5e5e5;
    // background: #f1f1f1;
    margin-bottom: 10px;
    .el-icon-s-help {
        color: #fb7299;
        font-size: 20px;
        line-height: 50px;
    }
    span {
        // display: inline-block;
        color: #fb7299;
        padding: 0 10px;
        font-size: 20px;
        // font-size: 20px;
    }
}

.swiper-container {
    position: relative;
    width: 1178px;
    height: auto;
    margin: 15px auto;
    padding: 10px 10px;
    overflow: hidden;
    background-color: #28374a;
    background: -webkit-linear-gradient(top,#28374a,#34425a 80px) #28374a;
    border: 1px solid #28374a;
    box-shadow: 0 10px 13px rgba(0,0,0,0.1), 0 0 1px rgba(0,0,0,0.1);
}

.swiper-slide {
    position: relative;
    height: 100px;
    border-radius: 4px;
    overflow: hidden;
    // transition: all .5s;

    /* Center slide text vertically */
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;
    transition-property: all;
    a {
        position: relative;
        position: absolute;
        width: 100%;
        height: 100%;
        cursor: pointer;
        img {
            width: 100%;
            height: 100%;
            // 自适应填满盒子
            object-fit: cover;
            transition: all 0.5s;
            background: url('../../assets/null.jpg') no-repeat center;
            background-size: cover;
        }
        .t-text {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            width: 80%;
            height: 24px;
            text-align: center;
            line-height: 24px;
            color: #fff;
            font-size: 14px;
            text-shadow: 0px 0px 6px rgba(0,0,0,0.6);
            transition: all 0.5s;
            overflow: hidden;
            z-index: 1;
        }
    }
}
// 遮罩层
.swiper-slide a::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: #000;
    opacity: 0.25;
    transition: all 0.5s;
    z-index: 1;
}
.swiper-slide a:hover::before {
    // 鼠标经过时,遮罩层全透明
    opacity: 0;
    transition: all 0.5s;
}
.swiper-slide:hover img {
    transform: scale(1.2);
}
.swiper-slide:hover .t-text {
    font-size: 16px;
}
// 左右切换按钮
.swiper-button-prev,
.swiper-button-next,
.swiper-button-all {
    position: absolute;
    top: 37px;
    right: 10px;
    width: 60px;
    height: 40px;
    border-radius: 4px;
    background-color: #34425a;
    // border: 1px solid #d3d4d6;
    box-shadow: 0 10px 13px rgba(0,0,0,0.1);
    
}

// 左侧按钮
.swiper-button-prev {
    left: 1048px;
}
.swiper-button-all {
    width: 70px;
    padding: 0px 15px;
    top: 15px;
    left: 931px;
    color: #99A9BF;
    font-size: 14px;
    text-align: center;
    line-height: 40px;
    overflow: hidden;
}
// 按钮左右箭头大小
.swiper-button-prev:after, 
.swiper-button-next:after {
    color: #99A9BF;
    font-size: 24px;
    font-weight: bold;
}
.swiper-button-prev.swiper-button-disabled, 
.swiper-button-next.swiper-button-disabled {
    // 没有更多时按钮全透明
    opacity: .5;
}
</style>