<template>
    <!-- 登录 -->
    <div class="user-login">
        <div class="el-tabs__header"></div>
        <!-- 消息提示 -->
        <transition name="el-zoom-in-center">
            <el-alert
                v-if="flag"
                :title="meaasge"
                type="error"
                center
                show-icon
                :closable="false"
            >
            </el-alert>
        </transition>
        <!-- 表单区域 -->
        <div class="form-login">
            <!-- 登录名称 -->
            <div class="form-group form-inline">
                <label for="username">账号</label>
                <input
                    type="text"
                    class="form-control"
                    placeholder="请输入用户名"
                    v-model="username"
                />
            </div>
            <!-- 登录密码 -->
            <div class="form-group form-inline">
                <label for="password">密码</label>
                <!-- 通过 ref 引用 获取用户密码 -->
                <input
                    type="password"
                    class="form-control"
                    placeholder="请输入密码"
                    v-model="password"
                />
            </div>
            <!-- 登录 -->
            <button type="button" class="btn-primary" @click="btn_login">
                登录
            </button>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            activeName: "first",
            username: "",
            password: "",
            meaasge: "",
            flag: false,
        };
    },
    methods: {
        // 请求用户登录
        btn_login() {
            this.$http.user
                .login({
                    username: this.username,
                    password: this.password,
                    flag: 1,
                })
                .then(({ code, data, msg }) => {
                    if (code === 200) {
                        // 登录成功
                        // 将 状态码 传递给父组件
                        this.$emit("getCode", code)
                        // 使用 Vuex 中的 store 来本地存储数据
                        // 使用 commit 来触发 mutations 中的 setToken 方法,并传参
                        this.$store.commit("setToken", data.token);
                        // 使用 dispatch 来触发 actions 中的 getUser 方法(请求用户信息)
                        this.$store.dispatch("getUser");
                    } else {
                        // 登录失败
                        localStorage.removeItem("vuex"); // 清除 token 信息
                        // 消息提示
                        this.flag = true;
                        this.meaasge = msg;
                    }
                });
        },
    },
};
</script>

<style lang="less" scoped>
.el-tabs__header {
    width: 100px;
    height: 100px;
    background: url("../../../assets/avatar.png");
    background-size: cover;
    border-radius: 50%;
    margin: 20px auto;
    // margin-bottom: 20px;
}
.form-login {
    width: 500px;
    height: 100%;
    // background-color: green;
    margin: 0 auto;
    .form-group {
        width: 100%;
        height: 40px;
        padding-top: 20px;
        display: flex;
        label {
            flex: 1;
            line-height: 40px;
            color: #fff;
        }
        .form-control {
            flex: 9;
            outline: 0;
            border-radius: 4px;
            border: 1px solid #dcdfe6;
            line-height: 40px;
            padding: 0 15px;
            font-size: 16px;
            font-weight: 700;
            font-family: "PingFang SC", "Lantinghei SC", "Microsoft YaHei",
                "Helvetica Neue", "Open Sans", Arial, "Hiragino Sans GB",
                sans-serif;
        }
    }
    .btn-primary {
        width: 100%;
        height: 40px;
        margin: 35px 0;
        border-radius: 4px;
        outline: none;
        border: 0;
        background-color: #eb4a76;
        opacity: 1;
        font-size: 16px;
        color: #fff;
        cursor: pointer;
        font-family: "PingFang SC", "Lantinghei SC", "Microsoft YaHei",
            "Helvetica Neue", "Open Sans", Arial, "Hiragino Sans GB", sans-serif;
        transition: all 0.5s;
    }
    .btn-primary:hover {
        opacity: 0.8;
    }
    
}
</style>